import React, { useState, useEffect } from 'react';
import { db } from '../firebase'; // Import your Firebase instance
import { collection, doc, getDoc } from 'firebase/firestore';

const ChatHeader = ({ toggleColumns, otherUserUID }) => {
  const [isMobile, setIsMobile] = useState(false);
  const [photoURL, setPhotoURL] = useState(null);
  const [displayName, setDisplayName] = useState(''); // New state variable
  const [occupation, setOccupation] = useState(''); // New state variable for occupation

  useEffect(() => {
    const mediaQuery = window.matchMedia('(max-width: 768px)');
    setIsMobile(mediaQuery.matches);

    const handleResize = () => {
      setIsMobile(mediaQuery.matches);
    };

    mediaQuery.addListener(handleResize);

    return () => {
      mediaQuery.removeListener(handleResize);
    };
  }, []);

  useEffect(() => {
    if (otherUserUID) {
      const firestore = db;

      // Check in expertApplications collection first
      const expertAppsRef = collection(firestore, 'expertApplications');
      const expertAppDoc = doc(expertAppsRef, otherUserUID);

      getDoc(expertAppDoc)
        .then((expertDocSnap) => {
          if (expertDocSnap.exists()) {
            const expertData = expertDocSnap.data();
            if (expertData) {
              setPhotoURL(expertData.photoURL);
              setDisplayName(expertData.displayName ? expertData.displayName : ''); // Set displayName
              setOccupation(expertData.occupation ? expertData.occupation : ''); // Set occupation
            }
          } else {
            // If not found in expertApplications, check userProfiles collection
            const userProfilesRef = collection(firestore, 'userProfiles');
            const userProfileDoc = doc(userProfilesRef, otherUserUID);

            getDoc(userProfileDoc)
              .then((docSnap) => {
                if (docSnap.exists()) {
                  const userData = docSnap.data();
                  if (userData) {
                    setPhotoURL(userData.photoURL);
                    setDisplayName(userData.displayName ? userData.displayName : ''); // Set displayName
                    setOccupation(userData.occupation); // Set occupation (might be undefined)
                  }
                }
              })
              .catch((error) => {
                console.error('Error getting user profile:', error);
              });
          }
        })
        .catch((error) => {
          console.error('Error getting expert application:', error);
        });
    }
  }, [otherUserUID]);

  return (
    <div className="chat-header p-4" style={{ display: 'flex', alignItems: 'center' }}>
      {isMobile && (
        <svg
          onClick={toggleColumns}
          xmlns="http://www.w3.org/2000/svg"
          color="#212529"
          fill="none"
          viewBox="0 0 24 24"
          strokeWidth={1}
          stroke="currentColor"
          height="25"
          width="25"
          style={{ marginRight: '8px' }} // Adjust margin as needed
        >
          <path strokeLinecap="round" strokeLinejoin="round" d="M10.5 19.5L3 12m0 0l7.5-7.5M3 12h18" />
        </svg>
      )}

      <div className="small-avatar">
        <img
          className="small-avatar mx-auto user-avatar mb-2"
          src={photoURL}
          alt="Custom 404 Image"
        />
      </div>
      <div className="d-flex flex-column justify-content-start offset-1 pt-2">
        <div>
          <p className="font-weight-bold mb-0">{displayName}</p>
          <p className="text-sm text-muted">{occupation}</p>
        </div>
      </div>
    </div>
  );
};

export default ChatHeader;
